import request from './http';

// 
export function getArticleDetail(id){
    return request({
        url:"article/"+id,
        method:"get"
    })
}

// 
export function getArticleNext(id){
    return request({
        url:"article/"+id+"/next",
        method:"get"
    })
}

/**
 * 
 * @param {Number} resource_id id
 * @param {Number} page 
 */
export function getArticleComments(params){
    return request({
        url:"/comments",
        method:"get",
        params
    })
}

// 
export function getArticleShort(mid){
    return request({
        url:"media/"+mid,
        method:"get"
    })
}